body {
  background-color: #fdf2e9;
}

.breed-select {
  display: none;
}

img {
  display: block;
}

.cat-info {
  max-width: 1200px;
  text-align: center;
  color: #922b21;
  column-gap: 40px;
  margin-top: 40px;
  display: flex;
}

.cat-info[hidden] {
  display: none;
}

.cat-text {
  font-weight: 700;
}

.error {
  color: red;
  display: none;
}

.loader-container {
  text-align: center;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader {
  width: 12px;
  height: 12px;
  color: #922b21;
  box-sizing: border-box;
  border-radius: 50%;
  margin: 15px auto;
  animation: animloader 2s linear infinite;
  display: block;
  position: relative;
}

@keyframes animloader {
  0% {
    box-shadow: 14px 0 0 -2px, 38px 0 0 -2px, -14px 0 0 -2px, -38px 0 0 -2px;
  }

  25% {
    box-shadow: 14px 0 0 -2px, 38px 0 0 -2px, -14px 0 0 -2px, -38px 0 0 2px;
  }

  50% {
    box-shadow: 14px 0 0 -2px, 38px 0 0 -2px, -14px 0 0 2px, -38px 0 0 -2px;
  }

  75% {
    box-shadow: 14px 0 0 2px, 38px 0 0 -2px, -14px 0 0 -2px, -38px 0 0 -2px;
  }

  100% {
    box-shadow: 14px 0 0 -2px, 38px 0 0 2px, -14px 0 0 -2px, -38px 0 0 -2px;
  }
}

